.chat {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  border-top: 1px solid grey;
  border-right: 1px solid grey;
  border-bottom: 1px solid grey;
  justify-content: space-between;
  margin: 0 auto;
}

.appBar {
  height: 100vh;
  width: 350px;
  overflow: auto;
  border: 1px solid grey;
}

.App {
  text-align: center;
  display: flex;
  height: 100vh;
}
